.page__footer {
  background-color: $basic_black;
  padding: 35px 17px 32px;

  @media (min-width: $desktop-width) {
    padding: 50px 0 40px;

    &-social__fb {
      width: 9px;
      height: 18px;
    }

    &-social__ok {
      width: 10px;
      height: 18px;
    }

    &-social__vk {
      width: 23px;
      height: 13px;
    }

    &-social__instagram {
      width: 19px;
      height: 19px;
    }
  }

  &-inner {
    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: 181px 209px;
      column-gap: 188px;
    }
  }

  &-social__list {
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;

    @media (min-width: $desktop-width) {
      justify-content: space-between;
      margin-bottom: 29px;
      margin-top: 4px;
    }
  }

  &-social__item {
    margin-right: 44px;

    @media (min-width: $desktop-width) {
      margin-right: 40px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &-applications {
    display: flex;
    justify-content: space-between;
    margin-bottom: 43px;

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }

    &__iphone {
      @media (min-width: $desktop-width) {
        width: 84px;
        height: 23px;
      }
    }

    &__android {
      @media (min-width: $desktop-width) {
        width: 93px;
        height: 22px;
      }
    }
  }

  &-text__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    letter-spacing: 0.2px;
    padding: 0 2px;

    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column: 1/3;
      column-gap: 168px;
      text-align: right;
      padding: 0;
      letter-spacing: 0;
    }
  }

  &-text__item {
    @media (min-width: $desktop-width) {
      margin-top: -7px;
    }
  }

  &-text__link {
    color: $basic_white;
    font-family: "HelveticaNeueCyr", "Arial", sans-serif;
    font-size: 10px;
    line-height: 15px;
  }

  &-text__contacts {
    font-family: 'Graphik LCG', sans-serif;
    font-size: 14px;
    line-height: 16px;
    margin-top: 8px;
    margin-bottom: 10px;
  }

}

.page__footer-text__item:first-child {
  opacity: 0.6;

  @media (min-width: $desktop-width) {
    opacity: 1;
  }
}

.item--support {
  grid-row: 2/2;
  grid-column: 1;

  @media (min-width: $desktop-width) {
    text-align: left;
    margin-top: -7px;
  }
}

.item--phone {
  grid-row: 1;
  grid-column: 1;

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}
