.achievements {
  margin-bottom: 70px;

  @media (min-width: $desktop-width) {
    margin-bottom: 114px;
  }

  &__subtext {
    font-size: 11px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 1.4px;
    margin-bottom: 5px;

    @media (min-width: $desktop-width) {
      font-size: 14px;
      margin-bottom: 12px;
    }
  }

  &__title {
    font-size: 25px;
    line-height: 32px;
    text-align: center;
    margin-bottom: 10px;
    padding: 0 10px;

    & br {
      display: none;
    }

    &:last-of-type {
      margin-bottom: 15px;

      @media (min-width: $desktop-width) {
        margin-bottom: 10px;
      }
    }

    @media (min-width: $desktop-width) {
      font-size: 35px;
      line-height: 42px;
      margin-bottom: 12px;
    }

    &--desktop {
      @media (min-width: $desktop-width) {
        font-size: 45px;
        line-height: 54px;
        padding: 0 50px;
      }
    }
  }

  &__img {
    display: block;
    margin: 0 auto 14px;
    object-fit: cover;

    @media (min-width: $desktop-width) {
      min-width: 580px;
      margin: 0 auto 20px;
    }
  }

  &__text {
    text-align: center;
    margin-bottom: 66px;
    letter-spacing: -0.2px;

    & br {
      display: none;
    }

    @media (min-width: $desktop-width) {
      font-size: 20px;
      line-height: 28px;
      margin-bottom: 110px;
    }

    &:last-of-type {
      margin-bottom: 30px;

      @media (min-width: $desktop-width) {
        &:last-of-type {
          margin-bottom: 40px;
          letter-spacing: -0.3px;
        }
      }
    }
  }

  &__container {
    display: grid;
    row-gap: 30px;

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 40px;
    }
  }

  &__card {
    text-align: center;

    @media (min-width: $desktop-width) {
      text-align: right;

      &:last-child {
        text-align: left;
      }
    }

    &-img {
      object-fit: cover;
      margin-bottom: 5px;

      @media (min-width: $desktop-width) {
        min-width: 270px;
        margin-bottom: 19px;
      }
    }

    &-text {
      font-size: 10px;
      line-height: 15px;
      letter-spacing: 1.4px;
      text-transform: uppercase;

      @media (min-width: $desktop-width) {
        font-size: 11px;
        line-height: 16px;
        margin-bottom: 7px;
      }
    }

    &-name {
      font-size: 18px;
      line-height: 29px;
      text-transform: uppercase;

      @media (min-width: $desktop-width) {
        font-size: 24px;
        line-height: 34px;
        margin-bottom: 4px;
        letter-spacing: 0.5px;
      }

      & span {
        font-size: 16px;
        line-height: 22px;
        text-transform: none;

        @media (min-width: $desktop-width) {
          font-size: 20px;
          line-height: 28px;
        }
      }
    }

    &-price {
      font-size: 17px;
      line-height: 24px;

      @media (min-width: $desktop-width) {
        font-size: 23px;
        line-height: 32px;
        margin-bottom: 4px;
        margin-right: 4px;
      }

      & s {
        color: #B3B3B3;
        margin-left: 17px;
        text-decoration: none;
        position: relative;

        @media (min-width: $desktop-width) {
          margin-left: 22px;
        }

        &::before {
          position: absolute;
          top: 0;
          left: -5px;
          content: "";
          width: calc(100% + 10px);
          height: 60%;
          border-bottom: 2px solid #B3B3B3;
        }
      }
    }

    &-buy {
      display: none;
      // position: relative;

      @media (min-width: $desktop-width) {
        display: block;
        font-size: 11px;
        line-height: 11px;
        letter-spacing: 1.4px;
        text-transform: uppercase;
        color: $basic_black;
        margin-right: 6px;

        & svg {
          position: relative;
          top: 3px;
          left: 6px;
        }
        // &::after {
        //   content: "";
        //   position: absolute;
        //   width: 15px;
        //   height: 15px;
        //   top: 7px;
        //   left: 100px;
        //   background-image: url("../img/arrow-right.svg");
        //   background-position: center;
        //   background-size: cover;
        // }
      }
    }
  }
}
