.idole {
  margin-bottom: 70px;

  @media (min-width: $desktop-width) {
    margin-bottom: 112px;
   }

  &__pic img {
    display: block;
    margin: 0 auto 20px;

    @media (min-width: $desktop-width) {
      min-width: 580px;
      object-fit: cover;
      margin-bottom: 25px;
    }
  }

  &__title {
    font-size: 30px;
    line-height: 39px;
    text-align: center;
    margin-bottom: 10px;

    @media (min-width: $desktop-width) {
      font-size: 45px;
      line-height: 54px;
      margin-bottom: 15px;
    }
  }

  &__text {
    text-align: center;
    margin-bottom: 30px;
    letter-spacing: -0.3px;

    @media (min-width: $desktop-width) {
      font-size: 20px;
      line-height: 28px;

      & br {
        display: none;
      }
    }
  }

  &__button {
    color: $basic_white;
    background-color: $basic_black;
    font-family: "HelveticaNeueCyr", "Arial", sans-serif;
    font-weight: 500;
    font-size: 11px;
    line-height: 11px;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    padding: 21px 0 18px;

    @media (min-width: $desktop-width) {
      font-size: 14px;
      line-height: 14px;
      padding: 18px 109px;
      max-width: 393px;
    }
  }
}
