.page {
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: $basic_black;

  // &__header {

  // }
}

.page__body {
  min-width: 320px;
  margin: 0 auto;

  // @media (min-width: $tablet-width) {
  //   min-width: 768px;
  // }

  @media (min-width: $desktop-width) {
    max-width: 800px;
  }
}

.page__nav {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 30px;
  // margin-bottom: 40px;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
    margin-top: 50px;
    margin-bottom: 54px;
  }

  &-list {
    flex-wrap: wrap;
    display: grid;
    grid-template-columns: repeat(5, auto);
    column-gap: 15px;
    font-family: 'Graphik LCG', sans-serif;
    font-size: 12px;
    line-height: 15px;
    margin: 0;
    padding: 19px 0 40px;

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(7, auto);
      column-gap: 30px;
      order: -1;
      padding: 9px 0 0;
    }
  }

  // &-item {
  //   // &--none {
  //   //   display: none;
  //   // }

  //   // &--active {
  //   //   color: #F10D48;
  //   // }
  // }

  &-item--none {
    display: none;

    @media (min-width: $desktop-width) {
     display: block;
    }
  }

  &-link {
    color: $basic_black;

    &--active {
      color: #F10D48;
    }
  }

  &-img {
    display: block;
    margin: 0 auto 20px;

    @media (min-width: $desktop-width) {
      width: 223px;
      // height: 55px;
      margin-bottom: 40px;
     }

  }
}
