.promo {
  background-color: #FFE9DA;
  padding: 30px 0 20px;
  margin-bottom: 70px;
  text-align: center;

  @media (min-width: $desktop-width) {
    max-width: 580px;
    margin: 0 auto 95px;
    padding: 30px 15px 20px;
  }

  &__subtext {
    font-size: 11px;
    line-height: 16px;
    font-weight: 500;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    opacity: 0.7;
  }

  &__title {
    font-size: 45px;
    line-height: 54px;
    letter-spacing: -0.2px;
    margin-bottom: 7px;

    @media (min-width: $desktop-width) {
      font-size: 50px;
      line-height: 60px;
      margin-bottom: 5px;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 18px;
    opacity: 0.7;
    margin-bottom: 25px;

    @media (min-width: $desktop-width) {
      margin-bottom: 15px;

      & br {
        display: none;
      }
    }

    &-small {
      font-size: 11px;
      line-height: 15px;
      opacity: 0.8;
    }
  }

  &__button {
    color: $basic_black;
    background-color: $basic_white;
    font-weight: 500;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 15px;

    @media (min-width: $desktop-width) {
      max-width: 280px;
      margin-bottom: 18px;
    }
  }
}
